import * as React from "react";
import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';
import Button from '@mui/material/Button';
import { mapping } from "./files/mapping";


const recordTypes = ["A", "CNAME", "MX", "TXT", "SRV"];
export const FormEntry: React.FC = () => {
    const zones = Object.keys(mapping.zones)
    const [selected, setSelected] = React.useState(false);
    const [value, setValue] = React.useState<string | null>("");
    const [inputValue, setInputValue] = React.useState('');


    return (
        <div style={{ paddingTop: "5%", display: "flex", flexDirection: "column", alignItems: "center" }}>

            <Autocomplete
                value={value}
                onChange={(event: any, newValue: string | null) => {
                    console.log("VALUE: ", newValue);
                    setValue(newValue);

                    if (newValue === null) {
                        console.log("Null");
                        setSelected(false);
                    } else {
                        console.log("Not");
                        setSelected(true);
                    }
                }}
                inputValue={inputValue}
                onInputChange={(event, newInputValue) => {
                    setInputValue(newInputValue);
                }}
                id="controllable-states-demo"
                options={zones}
                sx={{ width: 400, paddingBottom: "3%" }}

                renderInput={(params) => <TextField {...params} label="Zone" />}
            />
            <Autocomplete
                disablePortal
                style={{ alignItems: "center", textAlign: "center", display: selected ? "" : "none" }}
                id="combo-box-demo"
                options={recordTypes}
                sx={{ width: 400, paddingBottom: "3%" }}
                renderInput={(params) => <TextField {...params} label="Record Type" />}
            />
            <TextField id="NameField" sx={{ width: 400, paddingBottom: "3%" }} style={{ display: selected ? "" : "none" }} label="Name" variant="outlined" />
            <TextField id="ValueField" sx={{ width: 400, paddingBottom: "3%" }} label="Value" variant="outlined" style={{ display: selected ? "" : "none" }} />
            <Button variant="contained" size="large" disabled={!selected} style={{ backgroundColor: "#003C64" }}  >
                Submit Change
            </Button>
        </div >
    )
}
